
@media (min-width: 768px) and (max-width: 1023px) {
.fitcontent-sm {
    width: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
}
}
@media (max-width: 767px) {
.fitcontent-xs {
    width: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
}
}
.resume p {
  margin: 0;
}
.resume .category {
  margin-top: 2em;
}
.resume .list-topics:not(:last-child) {
  margin-bottom: 1.3em;
}
.resume .addtionalInformation {
  margin-bottom: 0.8em;
  text-align: justify;
}
.resume .header-info {
  height: auto;
  margin-left: 40px;
  margin-right: 40px;
  /*fine adjustiment*/
  border-radius: 3px;
}
@media (max-width: 767px) {
.resume .header-info {
      padding-bottom: 0.2em;
}
}
.resume .header-info .left-content, .resume .header-info .right-content {
    position: relative;
    left: -13.33333px;
}
@media (max-width: 767px) {
.resume .header-info .left-content, .resume .header-info .right-content {
        left: 0px;
}
}
.resume .header-info img {
    height: 170px !important;
    position: relative;
    -webkit-transform: scale(1.15);
            transform: scale(1.15);
    top: 0px;
    border-radius: 3px;
}
@media (max-width: 767px) {
.resume .header-info img {
        margin-right: auto;
        margin-left: auto;
        display: block;
        top: -20px;
}
}
.resume .header-info .left-side {
    padding-right: 1em;
}
.resume .category .right-side {
  padding-top: 5px;
  margin-left: 1.5em;
}
@media (max-width: 767px) {
.resume .category .right-side {
      margin-left: 0px;
}
}
.resume .list-link .button-link {
  display: block;
}
.resume .list-link .button-link:not(:first-of-type) {
  text-align: left;
}



